.info__boton {
  margin: 2rem 0;
  display: flex;
  justify-content: flex-end;
}

.boton {
  padding: 1.5rem 2rem;
  border-radius: 1rem;
  border: 1px solid #000000;
}

.boton:hover {
  color: #ffffff;
  background-color: #FF2E6D;
}

.info__botonnext {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  border: 1px solid rgb(50, 50, 50);
  border-radius: 50%;
  text-align: center;
  background-color: transparent;
  transition: all;
  transition-duration: 0.6s;
  font-size: 3rem;
}
.info__botonnext a {
  color: #ffffff;
}

.info__botonnext:hover {
  border-color: rgb(220, 220, 220);
  background-color: rgb(150, 150, 150);
}

.hero {
  width: 100%;
  height: 100vh;
  background-color: #0399FB;
  transition: all;
  transition-duration: 5s;
}
.hero.indice {
  background: linear-gradient(0deg, rgba(55, 14, 77, 0.8603816527) 0%, rgba(9, 9, 121, 0.5858718487) 48%, rgba(0, 212, 255, 0.4570203081) 100%), url("/build/img/globos.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}
.hero.artistas {
  background: linear-gradient(0deg, rgba(55, 14, 77, 0.8603816527) 0%, rgba(9, 9, 121, 0.5858718487) 48%, rgba(0, 212, 255, 0.4570203081) 100%), url("/build/img/artista.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
}
.hero .hero__titulo {
  margin: 10rem auto 0 auto;
  display: flex;
  flex-direction: column;
  color: #ffffff;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.titulo__secundario {
  color: #FFE200;
}

.hero__boton {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
  height: 5rem;
  border: 1px solid #FF8AAD;
  border-radius: 50%;
  text-align: center;
  margin: 0 auto;
  background-color: transparent;
  transition: all;
  transition-duration: 0.6s;
  font-size: 3rem;
}
.hero__boton a {
  color: #ffffff;
}

.hero__boton:hover {
  background-color: #FF2E6D;
  cursor: pointer;
}

.navegacion {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin: 0 auto;
  padding: 2rem;
  border-radius: 10px;
  text-align: center;
}
.navegacion .enlaces {
  font-size: 2.2rem;
  font-weight: 300;
}
.navegacion .enlaces a {
  padding: 0 1rem;
  color: #ffffff;
  transition: all;
  transition-duration: 1s;
}
.navegacion .enlaces a.actual {
  color: #ffffff;
}
.navegacion .enlaces a:hover {
  background-color: #FF2E6D;
}

.seccion {
  height: 100vh;
  position: relative;
}
.seccion .seccion__info {
  width: 100%;
  height: 35rem;
  background-color: rgba(255, 255, 255, 0.8);
  position: absolute;
  top: 30vh;
  padding: 5rem 40rem;
  box-shadow: rgba(145, 211, 254, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}
.seccion.artistas {
  background-image: url("/build/img/artista.jpg");
  justify-content: flex-start;
  background-position: center;
  background-attachment: fixed;
}
.seccion.eventos {
  background-image: url("/build/img/eventos.jpg");
  justify-content: flex-end;
}
.seccion.calendario {
  background-image: url("/build/img/calendario.jpg");
  justify-content: flex-start;
}

.info__boton {
  display: flex;
  justify-content: space-between;
}

.footer {
  width: 100%;
  background-color: #0399FB;
  display: flex;
  justify-content: space-around;
  padding: 2rem;
}
.footer .fenadu {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer p {
  color: #ffffff;
}
.genero {
  height: 30rem;
  padding: 5rem 20rem;
}
.genero .genero__titulo, .genero .genero__texto {
  color: #ffffff;
}
.genero.rosa {
  background-color: #FF004D;
}
.genero.azul {
  background-color: #0399FB;
}

.genero__lista {
  height: 50rem;
  width: 100%;
  position: relative;
}
.genero__lista.rosa {
  background: linear-gradient(180deg, rgba(255, 0, 77, 0.8855742981) 0%, rgba(255, 138, 173, 0.4345939059) 39%, rgba(14, 160, 255, 0.4570028695) 77%, rgba(58, 177, 255, 0.4654062309) 100%), url("/build/img/velaria.jpg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.genero__lista.azul {
  background: linear-gradient(180deg, rgba(255, 0, 77, 0.8855742981) 0%, rgba(255, 138, 173, 0.4345939059) 39%, rgba(14, 160, 255, 0.4570028695) 77%, rgba(58, 177, 255, 0.4654062309) 100%), url("/build/img/palenque.jpeg");
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.lista__cards {
  display: flex;
  column-gap: 3rem;
  justify-items: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.card {
  width: 30rem;
  background-color: rgba(255, 255, 255, 0.8);
  color: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
  height: 90%;
  border-radius: 1rem;
}
.card .card__imagen {
  width: 30rem;
  height: 18rem;
  overflow: hidden;
}
.card .card__titulo {
  text-align: center;
}
.card .card__titulo h3 {
  color: #000000;
  margin: 0;
}
.card .card__descripcion {
  padding: 2rem;
  text-align: justify;
}
.card .card__descripcion p {
  font-size: 1.7rem;
}

.card__footer {
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card__footer .card__fecha {
  font-weight: bold;
  color: #FE004D;
}

.boton__left, .boton__right {
  position: absolute;
  display: block;
  cursor: pointer;
  height: 100%;
  width: 6rem;
}

.boton__right {
  right: 0;
}

.boton__left {
  left: 0;
}

.boton__left::after, .boton__right::after {
  position: absolute;
  content: "❮";
  top: calc(50% - 1.5rem);
  font-size: 5rem;
  left: 2rem;
}

.boton__right::after {
  content: "❯";
}

.boton__left:hover {
  background: linear-gradient(90deg, rgba(0, 100, 164, 0.4542017491) 0%, rgba(145, 211, 254, 0) 100%);
}

.boton__right:hover {
  background: rgb(255, 0, 77);
  background: linear-gradient(270deg, rgba(255, 0, 77, 0.4542017491) 0%, rgba(255, 0, 77, 0) 90%);
}

.contenedor_calendario {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 2rem;
  padding: 0 2rem;
}

.evento {
  background-color: rgba(255, 46, 109, 0.9);
  color: #ffffff;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  padding: 1rem;
}
.evento .evento__titulo {
  margin: 0;
}
.evento .evento__info {
  display: grid;
  grid-template-columns: 1fr 1fr;
  background-color: #3AB1FF;
  height: 100%;
  padding: 1rem;
  border-radius: 2rem;
}

.calendario {
  background-color: rgba(58, 177, 255, 0.9);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  padding: 1rem;
}
.calendario .calendario__titulo {
  color: #ffffff;
  margin: 0;
}
.calendario .calendario__fechas {
  font-weight: bold;
}

.calendario__box {
  background-color: #FE004D;
  padding: 2rem;
  border-radius: 2rem;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.calendario__box .calendario__semana {
  background-color: #ffffff;
  border-radius: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  padding: 1rem;
  column-gap: 1rem;
  height: 6rem;
  align-items: center;
  align-content: center;
}
.calendario__box .calendario__semana .calendario__dia {
  font-weight: bold;
  background-color: #ffffff;
  line-height: 4rem;
  text-align: center;
  border-radius: 3rem;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  height: 4rem;
  transition: all 0.5s ease;
}
.calendario__box .calendario__semana .calendario__dia:hover {
  background-color: #FF2E6D;
  color: #ffffff;
  cursor: pointer;
}