@use 'variables' as v;
@use 'base/paleta' as color;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*::before,
*::after{
    box-sizing: inherit;
    margin: 0;
    padding: 0;
    border:0;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    &.fijar-body{
        overflow: hidden;
    }
    &.body-scroll{ 
        padding-top: 8.6rem;
    }
    
}

p{
    font-size: 2rem;
    color: color.$color-negro;
    line-height : 1.5;
    font-weight: 300;
}



.contenedor{
    width: 100%;
    /* max-width: 120rem; */
    margin: 0 auto;
}

a{
    text-decoration: none;
    color: color.$color-negro;
}

h1,h2,h3{
    margin:0 0 3rem 0;
    font-weight: 300;
}

h1{ font-size: 4rem;}
h2{ font-size: 3.6rem;}
h3{ font-size: 2.5rem;}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}